


























import Vue from "vue";
import {
  garageObs,
  getActivePlayer,
  getCurrentLobbyID,
  reset,
} from "@/common/board";
import { getWebSocket } from "@/common/websocket";

export default Vue.extend({
  name: "Garage",

  computed: {
    garageChanged: () => {
      return garageObs.garage;
    },
  },
  methods: {
    endGame() {
      const lobbyId = getCurrentLobbyID();
      console.log(lobbyId);
      if (lobbyId) {
        let end = JSON.stringify({
          type: "endGame",
          lobbyID: lobbyId,
        });
        let leave = JSON.stringify({
          type: "leaveLobby",
          lobbyID: lobbyId,
          playerName: getActivePlayer(),
        });
        getWebSocket().send(leave);
        getWebSocket().send(end);
        console.log(end);
        console.log(leave);
        reset();
        this.$router.replace({ name: "InitGame" });
      }
    },
  },
});
